import { CanActivateFn, Router } from '@angular/router';
import { getAllRouteParams, isDefined } from '../../../shared/utils';
import { inject } from '@angular/core';

export const deviceTypesGuard: CanActivateFn = (route, _state) => {
  const router = inject(Router);

  // Get all parameters including those from parent routes
  const allParams = getAllRouteParams(route);
  const superAdminId = allParams['superAdminId'];

  if (isDefined(superAdminId)) {
    return router.createUrlTree([
      'admin',
      'super-admin',
      superAdminId,
      'device-types',
      'grid',
      'super-admin-device-types',
    ]);
  }

  // If no superAdminId, don't allow access to the route
  return false;
};
