import { Routes } from '@angular/router';
import { createGuardedGridRoute } from '../../../../shared/utils';
import { distributorsGuard } from '../../guards';
import { deviceTypesGuard } from '../../guards/device-types.guard';
import { AdminGridContainerComponent } from '../admin-grid-container/admin-grid-container.component';
import { AdminTreetableContainerComponent } from '../admin-treetable-container/admin-treetable-container.component';
import { SystemAnalyticsContainerComponent } from '../system-analytics/system-analytics-container/system-analytics-container.component';
import { SystemSettingsContainerComponent } from '../system-settings/system-settings-container/system-settings-container.component';
import { SuperAdminContainerComponent } from './super-admin-container/super-admin-container.component';

export const SUPER_ADMIN_ROUTES: Routes = [
  {
    path: '',
    component: SuperAdminContainerComponent,
    children: [
      {
        path: '',
        redirectTo: 'distributors',
        pathMatch: 'full',
      },
      {
        path: 'system-health',
        loadChildren: () =>
          import('../system-health/system-health.routes').then(
            (m) => m.ADMIN_SYSTEM_HEALTH_ROUTES,
          ),
      },
      {
        path: 'analytics',
        component: SystemAnalyticsContainerComponent,
      },
      {
        path: 'settings',
        component: SystemSettingsContainerComponent,
      },
      {
        path: 'accounts',
        children: [
          {
            path: '',
            redirectTo: 'system-users',
            pathMatch: 'full',
          },
          {
            path: ':type',
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            component: AdminGridContainerComponent,
          },
        ],
      },
      ...createGuardedGridRoute(
        'distributors',
        distributorsGuard,
        AdminGridContainerComponent,
      ),
      ...createGuardedGridRoute(
        'device-types',
        deviceTypesGuard,
        AdminTreetableContainerComponent,
      ),
    ],
  },
];
